// > box heading
.box-heading.general {
    .main {
        height: 22px;
        overflow: hidden;
        line-height: 22px;
        border-left: 5px solid $third-color;
        padding-left: 10px;
        span{
            text-transform: uppercase;
            display: block;
            @include font(18, 22);
            color: $third-color;
        }
    }
}

// > timeline-stories

.timeline-box {
    .box-heading.general{
    }
    .box-content{
        padding-top: 15px;
    }
    .story {
        @include first-y;
        padding-top: 14px;
    }
    .story__heading {
        @include font(14, 20);
        .text {}
        margin-left: 150px;
    }
    .story__thumb{
        width: 135px;
        height: 75px;
        @include runaround(left, 3, 15, 0, 0);
    }
    .story__meta{
        margin-left: 150px;
    }
    /*--------*/
    .story--infographic {
        .story__cate {
            background-image: linear-gradient(-90deg, #3023AE 0%, #53A0FD 48%, #B4EC51 100%);
        }
    }
    .story--big {
        .story__thumb {
            width: 100%;
            height: 0;
            padding-bottom: 55.4%;
            overflow: hidden;
            margin: 0 0 10px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(30, 36);
            margin-left: 0;
            span{
                color: $first-color;
            }
        }
        .story__meta {
            margin-left: 0;
            display: inline-block;
        }
        .story__cate {
            margin-top: 2px;
            margin-left: 10px;
        }
    }
    .story--border {
        border-top: 1px solid #e8e8e8;
        padding-top: 10px;
        margin-top: 10px;
        .story__thumb {
            display: none;
        }
        .story__heading {
            @include font(16, 22, 500);
            .text {}
            margin-left: 0;
        }
        .story__meta{
            display: none;
        }
    }
    &.big-thumb{
        .story--big {
            border-top: 1px solid #ddd;
            padding-top: 15px;
            margin-top: 10px;
            .story__heading {
                @include font(16, 22, 500);
            }
            @include first-y;
        }

    }
}

